
.notice {
  width: 300px;
  height: 40px;
  line-height: 40px;
  position: absolute;
  top: 0%;
  left: 50%;
  margin-left: -50px;
  background: #00ff00;
}
.notice p {
  text-align: center;
}
.success {
  color: #3c763d;
  background: #dff0d8;
  -webkit-box-shadow: 0px 0px 10px #46b855;
          box-shadow: 0px 0px 10px #46b855;
}
.warning {
  color: #8a6d3b;
  background: #fcf8e3;
  -webkit-box-shadow: 0px 0px 10px rgba(129, 148, 95, 0.71);
          box-shadow: 0px 0px 10px rgba(129, 148, 95, 0.71);
}
.error {
  color: #a94442;
  background: #f2dede;
  -webkit-box-shadow: 0px 0px 10px #F77B6F;
          box-shadow: 0px 0px 10px #F77B6F;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}



